* {
  margin: 0;
}
.app__dropdown {
  margin-left: 40%;
  display: flex;
}

.options {
  background: whitesmoke;
}

.app__header {
  display: flex;
  align-items: center;
  background-color: black;
}
.removedeco {
  cursor: pointer;
  text-decoration: none;
  color: aliceblue;
}
.graph {
